<template>
  <div class="invite">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>我的邀请</span>
    </header>
    <section class="invite_top">
      <img src="../../assets/person/person.png" alt="">
      <section>
        <p>已邀请</p>
        <strong>{{totalNum}}人</strong>
      </section>
    </section>
    <section class="invite_wrapper">
      <header class="invite_header">
        <img src="../../assets/person/round_l.png" alt="">
        <span>邀请记录</span>
        <img src="../../assets/person/round_r.png" alt="">
      </header>
      <ul  class="content" v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
        <li class="record_list" v-for="i in list">
          <p>{{i.nickname}}</p>
          <p style="color: #ff6f00">{{i.z_yeji}}</p>
        </li>
        <li class="more_loading" v-if="!queryLoading && list.length !== 0">
          <mt-spinner type="fading-circle"  :size="25" v-show="moreLoading&&!allLoaded"></mt-spinner>
          <span v-show="allLoaded">已全部加载</span>
        </li>
      </ul>
    </section>
  </div>
</template>
<script>
export default {
  data(){
    return {
      list:[],
      moreLoading: false,
      queryLoading: false,
      allLoaded: false,
      pageSize: 20,
      pageNum: 1,
      totalNum: 0,
    }
  },
  methods: {
    loadMore(){
      if(this.allLoaded){
        this.moreLoading = true;
        return;
      }
      if(this.queryLoading){
        return;
      }
      this.moreLoading = !this.queryLoading;
      this.pageNum++;
      this.getShare(this.pageNum);
    },
    //获取分享
    back(){
      this.$router.go(-1);
    },
    getShare(page = 1){
      this.post('home/User/getShareUser', {
        page: page,
        number:20
      }).then(res => {
        if (res.data.code === 10000) {
          this.totalNum = res.data.info.number;
          this.list = this.list.concat(res.data.info.info);
          this.allLoaded = this.list.length=== this.totalNum;
          this.moreLoading = this.allLoaded;
        }
      })
    },
  },
  mounted(){
      this.getShare();
  }
}
</script>
<style scoped>
@import "../../../static/header.css";
.invite {
  height: 100vh;
  background-color: #F2F2F2;
  position: relative;
}
  .invite_top {
    display: inline-block;
    width: 100vw;
    height: 2.4rem;
    background-color: #ff6f00;
    position: relative;
  }
  .invite_top img {
    width: 2.13rem;
    height: 1.79rem;
    position: absolute;
    bottom: 0;
    left: 0.8rem;
  }
  .invite_top section {
    position: absolute;
    top: 0.46rem;
    left: 70%;
  }
  .invite_top p {
    font-size: 0.26rem;
    color: #FFFFFF;
  }
  .invite_top strong {
    font-size: 0.48rem;
    color: #FFFFFF;
  }
  .invite_wrapper {
    position: absolute;
    width: 96vw;
    height: 72%;
    background-color: #FFFFFF;
    border-radius: 0.1rem;
    top: 2.8rem;
    left: 2vw;
    text-align: center;
    padding: 0 0.2rem;
    overflow-y: auto;
  }
.invite_header {
  height: 0.88rem;
  line-height: 0.88rem;
}
  .invite_header img {
    width: 1.02rem;
    margin: 0 0.1rem;
  }
.invite_header span {
  font-size: 0.3rem;
  color: #cf9f82;
}
.invite_wrapper ul {
  margin-top: 0.2rem;
}
  .record_list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 0.6rem;
    line-height: 0.6rem;
    border-bottom: 1px solid #f2f2f2;
  }
  .record_list p:first-child {
    color: #333333;
    font-size: 0.26rem;
  }
  .record_list p:nth-child(3) {
  color: #666666;
  font-size: 0.26rem;
}
  .more_loading{
    height: 0.6rem;
    line-height: 0.6rem;
    display: flex;
    justify-content: center;
  }
</style>



